import React from "react";
import { useState, useRef } from "react";
import "./styles.css";

function App() {
  const inputTask = useRef(null);
  const [todoItems, setTodoItems] = useState([]);

  const addTotoItem = () => {
    inputTask.current.value &&
      setTodoItems([
        ...todoItems,
        { currentTask: inputTask.current.value, completed: false }
      ]);
    inputTask.current.value = "";
  };

  const deleteTask = (taskToBeDeleted) => {
    setTodoItems(
      todoItems.filter((task) => {
        return task.currentTask !== taskToBeDeleted;
      })
    );
  };

  const completeTask = (taskToComplete) => {
    setTodoItems(
      todoItems.map((todoItem) =>
        todoItem.currentTask === taskToComplete
          ? { currentTask: taskToComplete, completed: true }
          : { currentTask: todoItem.currentTask, completed: false }
      )
    );
  };

  return (
    <div className="container w-100 mt-5">
      <h5>To do List</h5>
      <div className="mb-3">
        <input
          ref={inputTask}
          type="textbox"
          className="form-control"
          placeholder="Task"
        ></input>
      </div>
      <button type="button" className="btn btn-light" onClick={addTotoItem}>
        Add Task
      </button>
      <hr></hr>
      <ul className="list-Item p-0">
        {todoItems.map((item, key) => {
          return (
            <li
              className="d-flex p-3 justify-content-around align-items-start flex-column"
              key={key}
            >
              <div className="d-flex flex-column ">
                <div>{item.currentTask.toUpperCase()}</div>
                <div>
                  {item.completed ? (
                    <span>Task Completed</span>
                  ) : (
                    <span>Task Not Completed</span>
                  )}
                </div>
              </div>
              <div className="d-flex  w-100">
                <div className="justify-content-end d-flex" style={{ flex: 1 }}>
                  <span
                    role="button"
                    className="me-2"
                    onClick={() => {
                      completeTask(item.currentTask);
                    }}
                  >
                    Complete
                  </span>
                  &nbsp; &nbsp;
                  <span
                    className="ms-3"
                    role="button"
                    onClick={() => {
                      deleteTask(item.currentTask);
                    }}
                  >
                    Delete
                  </span>
                </div>
              </div>
            </li>
          );
        })}
      </ul>
    </div>
  );
}
export default App;
